@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: #000;
}



#logo{
    margin: 10vh auto;
    display: flex;
    height: 12vh;
}
#LoginCard{
    width: 30vw;
    height: auto;
    margin: 10vh auto;
    padding: 2em;
    border-radius: 18px;
    background-color: #fff;
    color: #000;
}


@media (max-width: 768px){
    #LoginCard{
        width: auto;
        height: auto;
        margin: 2vh 18px;
        padding: 2em;
        border-radius: 18px;
        background-color: #000;
        color: #fff;
    }
}
nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 3vh;
  background-color: #000;
  flex-basis: 1 1;
}
.nav-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8vh;
  background-color: #000;
}

.acc-logo{
  float: right;
  display: flex;
  flex-flow: row;
  justify-content: center;
}

/* Views */
.views {
  display: flex;
  flex-flow: row;
}

/* TreeView CSS Section 1 */
.tree-view {
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  background-color: #fff;
  width: 20vw;
  overflow-y: scroll;
  height: 85vh;
  margin: 1.5vh;
  position: relative;
  overflow: hidden;
  border-radius: 12px;
}

.pre-view {
  display: flex;
  flex-flow: column;
  background-color: #fff;
  width: 80vw;
  height: 85vh;
  position: relative;
  overflow-y: scroll;
  padding: 1vh;
  margin: 1.5vh;
  border-radius: 12px;
}

/* ActionView CSS Section 2 */
.action-view {
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  background-color: #fff;
  width: 40vw;
  height: 85vh;
  margin: 1.5vh;
  overflow-y: scroll;
}

.more-info-page {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  height: -webkit-max-content;
  height: max-content;
  width: max-width;
}

.images-box {
  width: 100%;
  border-radius: 30px;
  margin-top: 20px;
}

.image {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%;
  height: 60vh;
  border-radius: 16px;
}

.more-info-page .info-box {
  margin: 16px 0;
}

.head-text {
  font-size: 1.5em;
  font-weight: bold;
  margin-top: 1em;
  margin-bottom: 1em;
}

#dir {
  text-align: center;
}

.info-box {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  margin: 16px;
}

.row1 {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.row2 {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.title {
  margin: auto 0;
  font-size: 2em;
}

.sub-text {
  margin: auto 5px;
}

.price {
  color: red;
  font-size: 1.5em;
  font-weight: 700;
}

.location {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin: 5px 0;
}

.location .sub-text {
  color: #e25214;
}

.split-sub-data-more {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  flex: 1 1;
}

.sub-data {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  flex: 1 1 65%;
}

.contactbtn {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: left;
}

.contactbtn button {
  width: 20%;
  height: 50px;
}

.service {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  flex: 0 2;
}
.service-box {
  display: flex;
  flex-flow: row;
  /* margin-right: 25px; */
  align-items: center;
  padding: 5px;
  border: 1px solid black;
  border-radius: 8px;
  margin: 5px;
}

.service-box .sub-text {
  font-size: 16px;
}

.ico-tick {
  padding: 5px;
}

.call {
  height: 20px;
  width: 20px;
  margin-right: 10px;
}

.split-sub-data-more {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  flex: 1 1;
  background-color: white;
}

.sub-data {
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  flex: 1 1 65%;
  background-color: white;

}


.tags {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  padding: 18px 0;
}

.tag-box {
  margin: 4px 4px;
  border-radius: 8px;
  background-color: rgba(0, 128, 0, 0.16);
  padding: 7px 14px;
  display: flex;
  flex-flow: row;
}

.tag-text {
  margin: auto 1px;
  color: green;
  font-weight: 500;
  font-size: 16px;
}

@media screen and (max-width: 700px) {
  .more-info-page {
    margin: 0 5vw 10vh;
  }

  .image {
    height: 35vh;
  }

  .contactbtn button {
    width: 100%;
    height: 50px;
    font-size: 1em;
  }
}

.tree-view-body{
    width: 100%;
}

.text-head{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 18px 0;
}

.list-item{
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    width: 100%;
    margin: 5px 0;
    padding: 5px;
    background-color: #f5f5f5;
}

.item-text{
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.item-action-key{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    align-items: center;
}

.list-btn{
    margin: 0 4px;
}

.add-btn{
    position: absolute;
    bottom: 0;
    width: 100%;
    border-radius: 0;
}

.form-box{
  position: relative;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.inner-form-box{
  border-radius: 24px;
  margin-top: 15vh;
  position: relative;
  background-color: #fff;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  height: 80vh;
  width: 50vw;
  overflow: scroll;
}


.form-head h1{
  margin: 20px;
  font-size: 2.5em;
}

.form-body{
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.form-inputs{
  margin: 25px;
}

.form-tags{
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin: 25px;

}

#del-ico{
  margin-left: 10px;
}

.preview-image{
  position: relative;
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  overflow: scroll;
}

.image-box{
  position: relative;
}
.preview{
  position: relative;
  height: 200px;
  width: 200px;
  margin: 10px 10px;
  border-radius: 8px;
}
.image-del{
  position: absolute;
  top: 0;
  right: 0;
  color: red;
  background-color: white;
  border-radius: 50%;
}
.no-image{
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 100%;
}

.submit{
  width: 100%;
}

@media screen and (max-width: 600px){
  .inner-form-box{
    width: 90vw;
  }
  .preview{
    height: 150px;
    width: 150px;
  }
}
